import React, { Component } from 'react';
import { Motion, spring } from 'react-motion';
import ReactDOM from 'react-dom';
//import logo from './logo.svg';
import './App.css';
import ReactPlayer from 'react-player';
import stupid2 from '../stupid2.mp3';

//import kawaii from '../kawaii.jpg';
class App extends Component {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.onKeyDwon = this.onKeyDown.bind(this);
        this.ranDom = this.ranDom.bind(this);
        this.end = this.end.bind(this);
        this.stop = this.stop.bind(this);
        this.GetKey = this.GetKey.bind(this);
        this.SoundChange = this.SoundChange.bind(this);
        this.home = this.home.bind(this);
        this.state={
            date: ['a','b','c','d','e','f','g','h','i','j','k','l','m','n','o','p','q','r','s','t','u','v','w','x','y','z'],
            url: ['https://youtu.be/ufPTWTa7xqk','https://youtu.be/yS2rcvR6tFI','https://youtu.be/FYZqitee64k'],
            start:false,
            on: '',
            rank: 0,
            change:0,
            leng: 1,
            lengspeed: 10,
            time: 10000,
            timerID: null,
            timerID2: null,
            play: false,
            errplay:false,
            score: 0,
            page: 0,
            keep: true,
            GetKey: null,
            list: []
        }
    }
    componentDidUpdate() {
        document.addEventListener("keydown", this.GetKey);
        //input
        //let input = ReactDOM.findDOMNode(this.refs.nameInput);
        //input && input.focus();
    }

    GetKey(e) {
        const l = e
        this.setState({ GetKey: l })
    }

    SoundChange() {
        const i = (this.state.change + 1) % 3
        this.setState({ change: i })
    }

    Create() {
            const list = [];
        const y = Math.floor(this.state.rank/2)
            for(var i=1;i<9+y;i++){
                const r = this.state.date[this.ranDom(this.state.date.length)]
                list.unshift(r);
            }
        return list
    }

    handleClick() {
        if ( this.state.play === true ){
          this.setState({ play: false,page: 0, score: 0 })
        }else{
         const l = this.Create()
         this.setState({ play: false, page: 1, list: l })
        }
    }

    home() {
        clearTimeout(this.state.timerID)
        this.setState({start: false, leng: 1, page: 0, play: false, score: 0, keep: true, rank:0, on: '' })
    }

    end() {
       this.setState({ page: 2 })
    }

    ranDom(max) {
      return  Math.floor(Math.random() * Math.floor(max));
    }
    stop() {
       this.setState({ errplay: false, timerID2: null })
    }

    onKeyDown(e) {
        console.log(e.keyCode)
        if(e.key === this.state.list[0]) {
            //
            if(this.state.start === true){
              const t = this.state.score + 10
              this.state.list.shift();
              const s = this.state.list
              if(s.length === 0 ){
                 s  = this.Create()
                 clearTimeout(this.state.timerID)
                 this.setState({ list: s, score: t, rank: this.state.rank + 1, keep: false, leng: 0, lengspeed: 1000 })
              }
            this.setState({ list: s, score: t })
            }
        }else if( e.key === 'Escape'){
            this.end()
        }else if( e.keyCode === 32){
            this.setState({ start: true, on:'', play: true, leng: 510, keep: false })
        }
        else{
            if(this.state.timeID2 !== null){
               clearTimeout(this.state.timerID2)
            }
            const t = this.state.score - 50
            var i = setTimeout(this.stop, 500)
            this.setState({ score: t, errplay:true, timerID2: i })
        }
    }

    render () {
        if (this.state.page === 0 ){
          return (
              <html id='speed'>
                  <input id='start' type="button" value='                   ' onClick={this.handleClick} />
                  <input type="button" value='change' onClick={this.SoundChange} />
              </html>
          )
      }else if(this.state.page === 1){
      //document.getElementById('sakura').focus();
        if(this.state.keep === false ){
            var timerID = setTimeout(this.end, this.state.time)
            console.log(this.state.keep)
            this.setState({ keep: true, timerID: timerID })
        }
        if(this.state.GetKey !== null){
              this.onKeyDown(this.state.GetKey)
              this.setState({ GetKey: null })
        }
          console.log(this.state.errplay)
        return (
          //scriptAPI
          //  <ReactPlayer url='../stupid2.mp3' playbackRate={1} width={0} height={0} volume={0.5} playing={true} loop />
          <div id='back'>
             <script src="https://www.youtube.com/iframe_api"></script>
             <p>{this.state.rank}</p>
             <p>{this.state.score}</p>
             <input type="button" value="stop" onClick={this.home} />
             <ReactPlayer url={this.state.url[this.state.change]} playbackRate={1} width={0} height={0} volume={1} playing={this.state.play} loop />
             <ReactPlayer ref={0} url='https://youtu.be/gNfkg3jtU_8' playbackRate={1} width={0} height={0} volume={0.8} playing={this.state.errplay} />
             <Motion defaultStyle={{p: 0}} style={{p: spring(this.state.leng,{stiffness: this.state.lengspeed, damping: 26,precision: 0.1})}} >{(value) => {
                if(this.state.leng === 0){
                    if(value.p <= 0){
                        this.setState({ leng:510, lengspeed: 10 })
                    }
                    return <div id='gagi'><table id='gagicolor' width={0} /></div>
                }
      return <div id='gagi' >
        <table id='gagicolor' width={value.p} />
      </div>
    }}</Motion>
<p>{this.state.on}</p>
         <p>{this.state.list}</p>
             </div>
      )
      }else{
          return (
              <div>
                  <p>{this.state.rank}</p>
                  <p></p>
                  <p>{this.state.score}</p>
                  <input type="button" value='home' onClick={this.home} />
              </div>
          )
      }
    }
}
export default App;



